body {
  margin: 0; }

.main-container .navigation-menu {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.219) 0px 5px 5px; }

.main-container .navigation-menu button {
  color: white;
  padding: 0.5rem 1rem;
  background-color: salmon;
  border: none;
  border-radius: 25px;
  cursor: pointer; }

.main-container .interactive-section {
  display: flex;
  padding: 1rem; }

.main-container .interactive-section .map {
  margin-right: 1rem;
  width: 70%;
  height: 600px; }

.main-container .interactive-section .appartment-list {
  width: 30%;
  padding: 1rem;
  box-shadow: rgba(0, 0, 0, 0.219) 0px 0px 10px; }

.main-container .interactive-section .appartment-list .appartment-card {
  margin-top: 1rem;
  padding: 1rem;
  border: rgba(0, 0, 0, 0.219) 1px solid;
  border-radius: 10px; }
